import { flattenObject } from 'lib/helperFunctions';

let flattenedImages = null;

const getFlattenedImages = () => {
  try {
    const images = {
      common: require('./common').default,
      fontawesome: require('./fontawesome').default,
      landing: require('./landing').default,
      socials: require('./socials').default,
    };

    return flattenObject(images);
  } catch (error) {
    // eslint-disable-next-line
  }

  return {};
};

const getImage = (key) => {
  if (!flattenedImages) {
    flattenedImages = getFlattenedImages();
  }
  const data = flattenedImages[key] || {};

  return {
    src: data.src || '',
    alt: data.alt || '',
  };
};

export {
  getImage,
};
